.view-assessments {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .view-assessments h2 {
    text-align: center;
  }
  
  .view-assessments ul {
    list-style-type: none;
    padding: 0;
  }
  
  .view-assessments li {
    background-color: #f9f9f9;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  